import React from "react";
import UndecoratedInternalLink from "components/reusable/atoms/links/UndecoratedInternalLink";
import { formatKebabCase } from "utils/formatting";

import {
  BlogPost,
  Image,
  PostDate,
  BlogTitle,
  Author,
  BlogCategory,
  Grey,
} from "./elements";

const BlogPreview = ({
  thumbnailImage,
  thumbnailImageRetina,
  postDate,
  postAuthor,
  postCategory,
  postLink,
  postTitle,
}) => (
  <BlogPost>
    <UndecoratedInternalLink to={`/blog/${postLink}`}>
      <Image
        thumbnailImage={thumbnailImage}
        thumbnailImageRetina={thumbnailImageRetina}
      />
    </UndecoratedInternalLink>
    <PostDate className="post-date">{postDate}</PostDate>
    <UndecoratedInternalLink to={`/blog/${postLink}`}>
      <BlogTitle>{postTitle}</BlogTitle>
    </UndecoratedInternalLink>
    {postAuthor && (
      <Author>
        <span>by </span>
        <UndecoratedInternalLink
          to={`/blog/author/${formatKebabCase(postAuthor.fields.name)}`}
        >
          <Grey>{postAuthor.fields.name}</Grey>
        </UndecoratedInternalLink>
      </Author>
    )}
    <UndecoratedInternalLink to={`/blog/${formatKebabCase(postCategory)}`}>
      <BlogCategory>{postCategory}</BlogCategory>
    </UndecoratedInternalLink>
  </BlogPost>
);

export default BlogPreview;
