import React from "react";
import { path } from "ramda";
import Layout from "components/layout";
import BlogEmailInput from "components/blog/BlogEmailInput";
import BlogList from "components/blog/categoryLanding/blogList";
import blogPreviewImage from "assets/img/the-log-preview-image.png";
import AuthorDescription from "components/blog/categoryLanding/authorDescription";
import { formatKebabCase } from "utils/formatting";

import { createBlogAuthorSchema } from "utils/seoSchema";

import {
  Sidebar,
  Category,
  Section,
  Main,
  CategoryContainer,
  CategoryTitle,
  MainTitle,
} from "./elements";

export default ({ location, pageContext }) => {
  const authorDetails = path(["authorDetails"], pageContext);
  const biography = path(["biography"], authorDetails);
  const linkedInLink = path(["linkedInHandle"], authorDetails);
  const twitterLink = path(["twitterHandle"], authorDetails);

  let schema;

  if (authorDetails) {
    const authorSchema = createBlogAuthorSchema(
      `https://brex.com/blog/author/${formatKebabCase(pageContext.title)}`,
      pageContext.title,
      // we slice because the URL for the picture begins with "//www.url.com"
      authorDetails.picture.fields.file.url.slice(2),
      [linkedInLink, twitterLink].filter((link) => link !== undefined),
      authorDetails.description,
    );

    schema = (
      <script type="application/ld+json">{JSON.stringify(authorSchema)}</script>
    );
  }

  return (
    <Layout
      whiteContentBackground
      location={location}
      title={pageContext.layoutTitle}
      description="Get data-driven insights, helpful guides, and trends for startups about setting up processes, hiring, growth, and making business decisions."
      ogImage={blogPreviewImage}
      script={authorDetails && schema}
    >
      <Section>
        <Main>
          <MainTitle>{pageContext.title}</MainTitle>
          {authorDetails && (
            <AuthorDescription
              biography={biography}
              linkedInLink={linkedInLink}
              twitterLink={twitterLink}
            />
          )}
          <BlogList contentfulPosts={pageContext.contentfulPosts} />
        </Main>
        <Sidebar>
          <CategoryContainer>
            <CategoryTitle>Categories</CategoryTitle>
            {pageContext.uniqueCategories.sort().map((category) => (
              <Category
                to={`/blog/${formatKebabCase(category)}`}
                key={category}
              >
                {category}
              </Category>
            ))}
          </CategoryContainer>
        </Sidebar>
        <BlogEmailInput backgroundColor="gray" />
      </Section>
    </Layout>
  );
};
