import React from "react";
import UndecoratedOutboundLink from "components/reusable/atoms/links/UndecoratedOutboundLink";

import linkedin from "assets/img/icons/logos/linkedin.svg";
import twitter from "assets/img/icons/logos/twitter.svg";

import { Container, Description, IconsContainer, Icon } from "./elements";

const AuthorDescription = ({ biography, linkedInLink, twitterLink }) => (
  <Container>
    <Description>{biography}</Description>
    <IconsContainer>
      {linkedInLink && (
        <UndecoratedOutboundLink
          href={linkedInLink}
          target="_blank"
          rel="noopener noreferrer"
        >
          <Icon src={linkedin} />
        </UndecoratedOutboundLink>
      )}
      {twitterLink && (
        <UndecoratedOutboundLink
          href={twitterLink}
          target="_blank"
          rel="noopener noreferrer"
        >
          <Icon src={twitter} />
        </UndecoratedOutboundLink>
      )}
    </IconsContainer>
  </Container>
);

export default AuthorDescription;
